.super-button {
  z-index: 200;
  width: 200px;
  height: 80px;
  opacity: .8;
  text-transform: uppercase;
  color: #8f8f8f;
  will-change: color, opacity, border-color;
  background-color: rgba(0, 0, 0, 0);
  border: 4px solid rgba(143, 143, 143, .5);
  font-size: 40px;
  font-weight: bold;
  transition: all 1.5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}

.super-button:hover {
  color: #fff;
  opacity: 1;
  border-color: #fff;
}

.super-button:hover ~ .overlay {
  background-color: rgba(155, 0, 0, .5);
}

.super-button:hover ~ .overlay.active {
  background-color: rgba(0, 0, 0, 0);
}

.super-button:hover ~ .button-line .inner {
  max-width: 100%;
}

.super-button.active {
  transition: all 4.5s;
  transform: translateX(-50%)translateY(-1000px);
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 100;
  will-change: background-color;
  background-color: rgba(155, 0, 0, 0);
  transition: all 1.5s;
  position: absolute;
  top: 0;
  left: 0;
}

.overlay.active {
  background-color: rgba(0, 0, 0, 0);
}

.button-line {
  top: -webkit-calc(50% - 2px);
  top: -moz-calc(50% - 2px);
  z-index: 200;
  width: -webkit-calc(50% - 100px);
  width: -moz-calc(50% - 100px);
  width: calc(50% - 100px);
  height: 4px;
  will-change: width, max-width;
  background-color: rgba(143, 143, 143, .5);
  transition: all .5s;
  position: absolute;
  top: calc(50% - 2px);
  left: 0;
}

.button-line.left {
  transform: rotate(180deg);
}

.button-line.right {
  left: -webkit-calc(50% + 100px);
  left: -moz-calc(50% + 100px);
  left: calc(50% + 100px);
}

.button-line .inner {
  width: 100%;
  height: 100%;
  max-width: 0;
  background-color: #fff;
  transition: all 1.5s;
